.info {

  display: grid;
  justify-content: center;
  grid-template-columns: max(754px);
  padding: 148px calc(50% - 500px);

  &__heading {

    font-family: $fontPoppins;
    font-weight: 600;
    font-size: 32px;
    line-height: 150%;
    justify-content: start;
  }

  &__text {
    font-family: $fontOpenSans;
    color: #6c788b;
  }
}
